.bar {
	min-width: 10rem;
	background-color: #ddd;
	height: .75rem;
	border-radius: .375rem;
	overflow: hidden;
}

.bar__inner {
	background-color: #888;
	height: 100%;
	border-radius: .375rem;
}

/* dark mode */
body.dark .bar {
	background-color: #111;
}
body.dark .bar__inner {
	background-color: #ff0;
}
